import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  Editor,
  EditorMenuMain,
  EditorGridType,
} from "../../editorObjects/Editor";
import { EditorChangeState } from "../Toolbar/EditorChangeState";
import { FieldCheckbox } from "../Toolbar/ui/FieldCheckbox";
import NumberFormat from "react-number-format";
import { Actions } from "../../store";

export const TopbarSecoundary = () => {
  const currentMenuMainOpen = useSelector((state: Editor) => {
    return state.ui.currentMenuMainOpen;
  });
  if (currentMenuMainOpen === EditorMenuMain.File) {
    return <SubMenuFile />;
  }
  if (currentMenuMainOpen === EditorMenuMain.Settings) {
    return <SubMenuSettings />;
  }
  return <React.Fragment />;
};

const SubMenuFile = () => {
  const dispatch = useDispatch();
  return (
    <div>
      <EditorChangeState />
    </div>
  );
};

const SubMenuSettings = () => {
  const dispatch = useDispatch();
  const grid = useSelector((state: Editor) => {
    return state.ui.grid;
  });

  const { height, width, hexSize, type, snapEnabled, isVisible } = grid;
  const onChange_isVisible = () => {
    dispatch(Actions.Topbar.action_toggleGridIsVisible());
  };
  const onChange_snapEnabled = () => {
    dispatch(Actions.Topbar.action_toggleGridSnapEnabled());
  };
  const onChange_gridItemW = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridWidth(newValue));
  };
  const onChange_gridItemH = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridHeight(newValue));
  };
  const onClick_selectTypeHex = () => {
    dispatch(Actions.Topbar.action_setGridType(EditorGridType.Hex));
  };
  const onClick_selectTypeBox = () => {
    dispatch(Actions.Topbar.action_setGridType(EditorGridType.Box));
  };
  const onChange_hexSize = value => {
    let newValue = value;
    if (value === undefined || value === null || isNaN(value) === true) {
      newValue = 1;
      return;
    }
    if (newValue < 1) {
      newValue = 1;
    }
    if (newValue > 100) {
      newValue = 100;
    }
    dispatch(Actions.Topbar.action_setGridHexSize(newValue));
  };

  return (
    <div style={{ display: "flex", flexWrap: "nowrap", alignItems: "center" }}>
      <GridOptions
        isVisible={isVisible}
        onChange_isVisible={onChange_isVisible}
        snapEnabled={snapEnabled}
        onChange_snapEnabled={onChange_snapEnabled}
        type={type}
        onClick_selectTypeHex={onClick_selectTypeHex}
        onClick_selectTypeBox={onClick_selectTypeBox}
      />

      {type === EditorGridType.Box ? (
        <GridBoxSettings
          gridItemW={width}
          onChange_gridItemW={onChange_gridItemW}
          gridItemH={height}
          onChange_gridItemH={onChange_gridItemH}
        />
      ) : null}

      {type === EditorGridType.Hex ? (
        <GridHexSettings
          hexSize={hexSize}
          onChange_hexSize={onChange_hexSize}
        />
      ) : null}
    </div>
  );
};

const GridOptions = ({
  isVisible,
  onChange_isVisible,
  snapEnabled,
  onChange_snapEnabled,
  type,
  onClick_selectTypeHex,
  onClick_selectTypeBox,
}) => {
  return (
    <React.Fragment>
      <div
        style={{
          marginRight: "15px",
        }}
      >
        <FieldCheckbox
          label={"Show grid"}
          value={isVisible}
          onChange={onChange_isVisible}
          isDisabled={false}
        />
      </div>

      <div
        style={{
          marginRight: "15px",
        }}
      >
        <FieldCheckbox
          label={"Snap to grid"}
          value={snapEnabled}
          onChange={onChange_snapEnabled}
          isDisabled={false}
        />
      </div>

      <div
        style={{
          marginRight: "15px",
        }}
      >
        <div className={"fieldset"}>
          <div className="label">Grid type: </div>
          <div className="control">
            <div
              className={` button is-light ${
                type === EditorGridType.Hex ? "is-dark" : ""
              } `}
              onClick={onClick_selectTypeHex}
            >
              <span>Hexagon</span>
            </div>
          </div>

          <div className="control">
            <div
              className={` button is-light ${
                type === EditorGridType.Box ? "is-dark" : ""
              }`}
              onClick={onClick_selectTypeBox}
            >
              <span>Rectangle</span>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

const GridBoxSettings = ({
  gridItemW,
  onChange_gridItemW,
  gridItemH,
  onChange_gridItemH,
}) => {
  return (
    <div
      style={{
        display: "flex",
        flexWrap: "nowrap",
        alignItems: "center",
        marginRight: "15px",
      }}
    >
      <div className={"fieldset"}>
        <div className="label">Grid size: </div>

        <NumberFormat
          allowNegative={false}
          allowEmptyFormatting={true}
          max={99}
          min={1}
          maxLength={2}
          value={gridItemW}
          onValueChange={event => {
            onChange_gridItemW(event.floatValue);
          }}
          placeholder="width"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "0px 10px",
            marginBottom: "0px",
            marginTop: "0px",
            maxWidth: "70px",
            marginRight: "5px",
          }}
        />
        <NumberFormat
          allowNegative={false}
          allowEmptyFormatting={true}
          max={99}
          min={1}
          maxLength={2}
          value={gridItemH}
          onValueChange={event => {
            onChange_gridItemH(event.floatValue);
          }}
          placeholder="height"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "0px 10px",
            marginBottom: "0px",
            marginTop: "0px",
            maxWidth: "70px",
          }}
        />
      </div>
    </div>
  );
};

const GridHexSettings = ({ hexSize, onChange_hexSize }) => {
  return (
    <div
      style={{
        display: "flex",
        flexWrap: "nowrap",
        alignItems: "center",
        marginRight: "15px",
      }}
    >
      <div className={"fieldset"}>
        <div className="label">Hex size: </div>

        <NumberFormat
          allowNegative={false}
          allowEmptyFormatting={true}
          max={99}
          min={5}
          maxLength={2}
          value={hexSize}
          onValueChange={event => {
            onChange_hexSize(event.floatValue);
          }}
          placeholder="width"
          style={{
            color: "#4a4a4a",
            textAlign: "center",
            border: "1px solid #aeaeae",
            fontFamily: "Open Sans, sans-serif",
            fontSize: "13px",
            fontWeight: 400,
            lineHeight: "1.5",
            padding: "0px 10px",
            marginBottom: "0px",
            marginTop: "0px",
            maxWidth: "70px",
            marginRight: "5px",
          }}
        />
      </div>
    </div>
  );
};
