import { Editor, EditorMenuMain } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";

export class TopbarImmerReducer extends ImmerReducer<Editor> {
  action_toggleMenuFile() {
    if (this.draftState.ui.currentMenuMainOpen === EditorMenuMain.File) {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.None;
    } else {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.File;
    }
  }
  action_toggleMenuSettings() {
    if (this.draftState.ui.currentMenuMainOpen === EditorMenuMain.Settings) {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.None;
    } else {
      this.draftState.ui.currentMenuMainOpen = EditorMenuMain.Settings;
    }
  }
  action_toggleGridSnapEnabled() {
    this.draftState.ui.grid.snapEnabled = !this.draftState.ui.grid.snapEnabled;
  }
  action_toggleGridIsVisible() {
    this.draftState.ui.grid.isVisible = !this.draftState.ui.grid.isVisible;
  }
  action_setGridWidth(value) {
    this.draftState.ui.grid.width = value;
  }
  action_setGridHeight(value) {
    this.draftState.ui.grid.height = value;
  }
  action_setGridType(value) {
    this.draftState.ui.grid.type = value;
  }
  action_setGridHexSize(value) {
    this.draftState.ui.grid.hexSize = value;
  }
}
